import React, {useState, useEffect} from "react";

const ScrollView = () =>{ 

    const [navFixed, setNavFixed] = useState("hidden")

    useEffect(_ =>{ 
        const scrollViewHeader = ()=>{ 
            if(window.pageYOffset > 1){
                setNavFixed("")
            }else{
                setNavFixed("hidden")
            }
        }

        window.addEventListener('scroll', scrollViewHeader)
        return _ => {
            window.removeEventListener('scroll', scrollViewHeader)
         }

    } , [])

    return(
        <section className="">
            <a href="#home"><div className={`fixed bottom-0 right-0 cursor-pointer mb-4 md:mb-8 mr-4 md:mr-8 ${navFixed}`}>
                <box-icon name='up-arrow-square' color="gray" size="lg" type='solid' ></box-icon>
            </div></a>
        </section>
    )
}

export default ScrollView;