import React, {useState, useEffect} from "react";
import { Icon } from '@iconify/react';

const Header = ({toggledarkmode, theme})=>{

    const [hambureger, sethamburger] = useState("")
    const [navMenu, setNavMenu] = useState("hidden")

    const toggleHamburger = ()=>{
        
        sethamburger(hambureger === "" ? "active" : "")
        setNavMenu(navMenu === "" ? "hidden" : "")

    }
    const [navFixed, setnavFixed] = useState("")

    useEffect(_ =>{
        
        const scrollHandler = ()=>{

            if(window.pageYOffset > 1){
                setnavFixed("navbar-fixed")
            }else{
                setnavFixed("")
            }
        }      
        
        window.addEventListener('scroll', scrollHandler)
        return _ => {
          window.removeEventListener('scroll', scrollHandler)
        }

    }, [])

    const toggleHandlear = ()=>{ 
        theme === 'light' ? toggledarkmode('dark') : toggledarkmode('light')

     }
    const DataHeader = [

        {
            id:1,
            name: 'Home',
            tag: 'home',
        },
        {
            id:5,
            name:'Experience',
            tag: 'experience',
        },
        {
            id:3,
            name: 'About',
            tag: 'about',
        },
        {
            id:2,
            name: 'Skills',
            tag: 'skills',
        },

        {
            id:4,
            name: 'Portfolio',
            tag: 'portfolio',
        },

        {
            id:6,
            name:'Contact',
            tag: 'contact',
        }
        
    ]

    return(
        <header className={`absolute top-0 left-0 z-10 flex items-center w-full bg-transparent dark:bg-slate-800 ${navFixed}`}>
             <div className="container">
                <div className="relative flex items-center justify-between">
                    <div className="flex dark:text-slate-100 mt-2 items-center">
                        <img src="/logo.png" width="60" className="block dark:hidden py-6 text-4xl font-bold text-utama"></img>
                        <img src="/logo_dark.png" width="60" className="hidden dark:block py-6 text-4xl font-bold text-utama"></img>
                        <p className="text-lg font-[550]">Fathoni Waseso Jati</p>
                    </div>
                    <div className="flex items-center px-4">
                        <button onClick={toggleHamburger}  id="hamburger" name="hamburger" className={`fixed block right-7 mt-1 ${hambureger} lg:hidden`}>
                            <span className="transition duration-500 ease-in-out origin-top-left hamburger-garis dark:bg-slate-100"></span>
                            <span className="transition duration-500 ease-in-out origin-top-left hamburger-garis dark:bg-slate-100"></span>
                            <span className="transition duration-500 ease-in-out origin-bottom-left hamburger-garis dark:bg-slate-100"></span>
                        </button>
                        <nav id="nav-menu" className={`${navMenu} absolute py-5 bg-white dark:bg-slate-800 font-bold shadow-lg rounded-lg max-w-[250px] w-full right-4 top-full lg:block lg:static lg:max-w-full lg:bg-transparent lg:shadow-none lg:rounded-none`}>
                            <ul className="block lg:flex">
                                {
                                    DataHeader.map((data) => (
                                    <li key={data.id} className="group hover:bg-utama duration-500 ease-in-out py-2 hover:-translate-y-[10px] ">
                                        <a href={`#${data.tag}`} className="flex py-2 mx-8 hover:text-white text-base text-hitam dark:text-slate-100 group-hover:text-white">{data.name}</a>
                                    </li>
                                    ))
                                }
                                    <li className="group flex justify-center items-center py-2">
                                    <input id="toggle_darkmode" onClick={toggleHandlear} type="checkbox" className="hidden" />
                                        <label htmlFor="toggle_darkmode">
                                            <div className="main-toggle flex bg-slate-300 items-center justify-start border-none rounded-full outline-none w-14 h-6  relative cursor-pointer">
                                                <div style={{backgroundColor:"#1f257f"}} className="toggle-moon transition  duration-500 ease-in-out relative w-5 h-5 rounded-full border-2 border-white  items-center">
                                                    <Icon className="absulute top-1/2 translate-x-0.5 text-white" icon="ri:moon-line" />
                                                </div>
                                            </div>
                                          
                                        </label>
                                    </li>
                            </ul>
                        </nav>
                    </div>
                </div>
             </div>   
        </header>
    )
    
};

export default Header;